
.ChestPainCenter {
  width: calc(100% - 200px);
  height: 100%;
  margin-left: 200px;
  overflow: hidden;
}
.ChestPainCenter .title {
  position: fixed;
  top: 5%;
  left: 52%;
}
.ChestPainCenter .title span {
  color: #3C9BFF;
  font-size: 0.3rem;
  font-family: PingFangSC-Medium;
  font-weight: 700;
}
.ChestPainCenter .inner {
  width: 100%;
  margin: 0 auto;
  height: 100%;
}
.ChestPainCenter .inner span {
  vertical-align: middle;
  font-size: 0.28rem;
  font-family: PingFangSC-Regular;
}
.ChestPainCenter .inner .information {
  width: 100%;
  border-bottom: 1px solid #eee;
  padding: 0.35rem 0;
  margin-top: 1rem;
  overflow: hidden;
}
.ChestPainCenter .inner .information .information_inner {
  margin-left: 30px;
}
.ChestPainCenter .inner .information .information_inner span {
  color: #999999;
}
.ChestPainCenter .inner .information .information_inner span.Patient {
  margin-right: 0.5rem;
  color: #333333;
}
.ChestPainCenter .inner .information .information_inner span.sex {
  margin-right: 0.12rem;
}
.ChestPainCenter .inner .information .information_inner b {
  margin-right: 0.3rem;
  font-size: 0.3rem;
  font-family: PingFangSC-Medium;
  vertical-align: middle;
}
.ChestPainCenter .inner .hospital {
  margin-top: 0;
}
.ChestPainCenter .inner .hospital .hospital_left {
  float: left;
}
.ChestPainCenter .inner .hospital .hospital_right {
  float: left;
  margin-left: 0.8rem;
}
.ChestPainCenter .inner .hospital .hospital_right span {
  padding: 0.13rem;
  color: #3C9BFF;
  border: 1px solid #3C9BFF;
  margin-left: 0.1rem;
  border-radius: 2px;
}
.ChestPainCenter .inner .hospital .hospital_right span.type {
  color: #333333;
  margin-right: 0.2rem;
  padding: 0;
  border: none;
}
.ChestPainCenter .inner .hospital .hospital_right span.active {
  color: white;
  background-color: #3C9BFF;
}
.ChestPainCenter .inner .ProblemDescription {
  width: 100%;
  border-bottom: 1px solid #eee;
  padding: 0.2rem 0;
  overflow: hidden;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner {
  margin-left: 30px;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_left {
  float: left;
  width: 60%;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_left span {
  color: #333333;
  margin-right: 0.5rem;
  vertical-align: top;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_left textarea {
  font-size: 0.28rem;
  font-family: PingFangSC-Regular;
  color: #999999;
  outline: none;
  border: none;
  width: 80%;
  vertical-align: top;
  position: relative;
  top: -0.1rem;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_right {
  float: left;
  margin-left: 2.5rem;
  width: 2rem;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_right .microphone {
  width: 2rem;
  height: 1.2rem;
  background: url(static/img/rectangle.4ddf798.png) no-repeat;
  background-size: contain;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_right .microphone img {
  width: 0.44rem;
  height: 0.74rem;
}
.ChestPainCenter .inner .ProblemDescription .ProblemDescription_inner .ProblemDescription_right p {
  font-size: 0.28rem;
  font-family: PingFangSC-Regular;
  color: #3C9BFF;
  margin-top: 0.1rem;
  text-align: center;
}
.ChestPainCenter .inner .report {
  width: 100%;
  padding: 0.4rem 0;
  overflow: hidden;
}
.ChestPainCenter .inner .report .report_inner {
  margin-left: 30px;
}
.ChestPainCenter .inner .report .report_inner .report_top {
  height: 1.4rem;
}
.ChestPainCenter .inner .report .report_inner .report_top .report_text {
  float: left;
}
.ChestPainCenter .inner .report .report_inner .report_top .report_text span {
  color: #333333;
  display: inline-block;
  width: 0.75rem;
  text-align: left;
}
.ChestPainCenter .inner .report .report_inner .report_top ul {
  float: left;
  margin-left: 0.9rem;
}
.ChestPainCenter .inner .report .report_inner .report_top ul li {
  width: 1.4rem;
  height: 1.4rem;
  float: left;
  margin-right: 0.2rem;
  border: 1px solid #3C9BFF;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ChestPainCenter .inner .report .report_inner .report_top ul li img {
  width: 0.53rem;
  height: 0.6rem;
}
.ChestPainCenter .inner .report .report_inner .report_top ul li span {
  color: #3C9BFF;
}
.ChestPainCenter .inner .report_bottom {
  width: 100%;
  text-align: center;
}
.ChestPainCenter .inner .report_bottom button {
  outline: none;
  width: 5rem;
  height: 1.2rem;
  background-color: #3C9BFF;
  color: white;
  font-family: PingFangSC-Medium;
  font-size: 0.48rem;
  text-align: center;
  line-height: 1.2rem;
  border-radius: 10px;
  border: none;
}
